.app {
  min-height: 100vh;
  overflow: auto !important;
  .root-container {
    overflow: auto !important;
  }
}
.custom-select {
  width: 100%;
  background-color: initial;
  height: 45px;
  font-size: 19px;
}

[type="radio"]:not(:checked) + span:before {
  border: none !important;
}
[type="radio"]:checked + span:before {
  border: none !important;
}

.container-form {
  font-size: 20px;
  margin: 40px 85px;
  table {
    border-collapse: collapse;
    width: 100%;
  }
  input {
    &:focus {
      outline: none;
    }
  }
  .custome-padd {
    padding: 0 15px;
  }
  .head-info-form {
    margin-top: 14px;
    &:after {
      display: block;
      content: "";
      clear: both;
    }
    p {
      display: flex;
      float: left;
    }
    .nguoitraloi-style {
      font-family: Muli;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 34px;
      color: #333333;
    }
    .user-info-radio2 {
      display: flex;
      .containerCheck2 {
        width: 50%;
        &:first-child {
          padding-right: 15px;
        }
        &:last-child {
          padding-left: 15px;
        }
        .checkmark2 {
          padding: 10px;
          height: 45px;
          font-weight: bold;
          color: black;
          text-align: center;
          width: 100%;
          border-radius: 10px;
          border: 1px solid #999999;
        }
      }
    }
  }
  .title-form {
    p {
      &:first-child {
        font-weight: bold;
        font-size: 27px;
        line-height: 36px;
        text-align: center;
        margin-bottom: 5px;
        color: #34458a;
      }
      &:last-child {
        text-align: justify;
        font-style: italic;
        font-size: 22px;
        line-height: 30px;
        color: #999999;
      }
    }
  }
  .form-info {
    border: 1px solid #999999;
    margin-bottom: 40px;
    .title-info-form {
      font-size: 20px;
      margin-bottom: 0;
      font-weight: bold;
      padding: 5px 10px;
      background: rgba(0, 203, 167, 0.1);
      color: #333333;
    }
    label {
      font-size: 20px;
      color: #333333;
    }
    .row {
      margin-bottom: 25px;
      align-items: center;
    }
    .user-info-inner {
      padding: 15px;
      .text-box-input {
        margin-bottom: 0;
      }
      .user-info-radio {
        display: flex;
        .containerCheck {
          width: 50%;
          &:first-child {
            padding-right: 15px;
          }
          &:last-child {
            padding-left: 15px;
          }
          .checkmark {
            padding: 10px;
            height: 45px;
            font-weight: bold;
            text-align: center;
            width: 100%;
            border-radius: 10px;
            border: 1px solid #999999;
          }
        }
      }
      .list-evaluate {
        padding: 0;
        display: flex;
        margin-bottom: 12px;
        color: #333333;
        li {
          width: 25%;
          line-height: 1.5;
          text-align: center;
        }
        span {
          padding: 0px 9px;
          border: 1px solid;
          border-radius: 50%;
          font-size: 22px;
          font-weight: bold;
        }
      }
    }
  }
  .evaluate {
    .tbl-eval {
      border: 1px solid #333333;
      tr {
        border-bottom: 1px solid rgba(0, 0, 0, 0.12);
        td {
          font-weight: 600;
          &:first-child {
            text-align: center;
          }
        }
      }
      .title-table-eval {
        background: linear-gradient(90deg, #34458a 0%, #9b51e0 126.56%);
        color: #ffffff;
        text-transform: uppercase;
        padding: 10px 0 10px 20px;
        border: 1px solid #000;
        font-weight: bold;
        text-align: initial !important;
      }
      .display-radio {
        display: flex;
        align-items: center;
        padding: 0 25px;
        margin-bottom: 8px;
        margin-top: 7px;
        justify-content: space-around;
        label {
          span {
            height: 34px;
            padding: 4px 9px;
            border: 1px solid #333333;
            border-radius: 50%;
            font-size: 22px;
            font-weight: bold;
          }
        }
      }
      .hospital-assessment {
        position: relative;
        span {
          position: absolute;
          right: 14px;
          top: 29%;
        }
      }
      .textarea-reason{
        min-height: 100px;
        border-radius: 5px;
        padding: 5px 10px;
      }
    }
    .noteForm {
      text-align: right;
      font-weight: bold;
      margin-top: 7px;
      color: #34458a;
    }
  }
}

.container-form .containerCheck {
  display: block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0 !important;
}

.container-form .containerCheck input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  left: -1px;
}
.container-form .containerCheck span {
  color: #000;
}
.container-form .checkmark {
  position: absolute;
  top: 0;
  left: 0;
}
.container-form .user-info-radio .containerCheck input:checked ~ .checkmark,
.container-form .display-radio .containerCheck input:checked ~ .checkmark {
  background: linear-gradient(90deg, #ed166b 0%, #f0bd33 126.56%);
  color: #fff;
}
.container-form .user-info-radio2 .containerCheck2 input:checked ~ .checkmark2,
.container-form .display-radio .containerCheck2 input:checked ~ .checkmark2 {
  background: linear-gradient(90deg, #ed166b 0%, #f0bd33 126.56%);
  color: #fff;
}

.container-form .user-info-radio .containerCheck input:checked ~ .checkmark {
  background: linear-gradient(90deg, #ed166b 0%, #f0bd33 126.56%);
  color: #fff;
}
.container-form .user-info-radio .containerCheck input:checked ~ .checkmark {
  background: linear-gradient(90deg, #ed166b 0%, #f0bd33 126.56%);
  color: #fff;
}
.container-form
  .form-info
  .user-info-inner
  .user-info-radio
  .containerCheck
  input:checked
  ~ .checkmark {
  background: linear-gradient(90deg, #ed166b 0%, #f0bd33 126.56%);
  color: #fff;
}

.container-form .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.container-form .checkmark2:after {
  content: "";
  position: absolute;
  display: none;
}

.submit-form {
  text-align: right;
  margin-top: 20px;
  button {
    background: linear-gradient(90deg, #34458A 0%, #9B51E0 126.56%);
    padding: 18px 120px;
    border: none;
    border-radius: 5px;
    >div {
      display: flex;
      align-items: center;
      span {
        font-weight: bold;
        font-size: 27px;
        color: #fff;
      }
      img {
        margin-left: 0.2em;
      }
    }
  }
  .noteForm {
    margin-top: 50px;
    font-family: Muli;
    font-style: normal;
    font-weight: 700;
    font-size: 29px;
    line-height: 36px;
    align-items: center;
    text-align: right;
    color: #34458A;
  }
  .logoFooter {
    margin-top: 1.5em;
  }
}
.display-radio-last {
  .containerCheck {
    padding-left: 0;
    color: #000;
    .checkmark {
      padding: 8px 10px;
      font-size: 20px;
      color: black;
      border: 1px solid #999999;
      border-radius: 10px;
      width: 100%;
      height: 47px;
      input {
        left: 160px;
        top: 0;
        opacity: 1;
        border: none;
        margin: 0;
        &:focus {
          outline: none;
          border: none;
          box-shadow: none;
        }
      }
    }
  }
}
.display-radio-last .containerCheck input:checked ~ .checkmark {
  background: linear-gradient(90deg, #ed166b 0%, #f0bd33 126.56%);
  color: #fff;
}
.display-radio-last .containerCheck input:checked ~ .checkmark input {
  color: #fff;
}

.style-total-nam-vien {
  display: flex;
  border: 1px solid #999999 !important;
  padding: 0 18px 0 18px;
  border-radius: 10px;
  input {
    font-size: 20px !important;
    padding: 0 !important;
    margin: 0 !important;
    border: none !important;
    color: #333;
  }
  input::placeholder {
    color: #7a7a7a;
  }
}

.styine-area {
  height: 196px;
  border: 1px solid #999999;
  border-radius: 10px;
  padding: 14px;
}

.style-total-dropdow {
  text-align: center;
  .stylee-dropdow-btn {
    border: none;
    margin-top: 40px;
    font-style: normal;
    text-transform: inherit;
    font-weight: normal;
    width: 324px;
    text-align: left;
    height: 60px;
    background: linear-gradient(90deg, #34458a 0%, #9b51e0 126.56%);
    font-size: 26px;
  }
  .style-drop-dow-item {
    font-style: normal;
    width: 324px;
    height: 60px;
    color: white;
    font-weight: normal;
    text-align: left;
    background: linear-gradient(90deg, #34458a 0%, #9b51e0 126.56%);
    font-size: 26px;
  }
  .style-drop-dow-item:hover {
    background: #ffffff;
    color: #666666;
  }
}